.background-image {
  position: fixed;
  inset: 0px;
  box-sizing: border-box;
  padding: 0px;
  border: none;
  margin: auto;
  display: block;
  width: 0px;
  height: 0px;
  min-width: 100%;
  max-width: 100%;
  min-height: 100%;
  max-height: 100%;
  object-fit: cover;
  z-index: -1;

}


.hero-container {
  height: 100vh;
  width: 100%;
  margin-top: -60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  object-fit: contain;

}

.welcome-text {
  /* margin-top: -100px; */
  max-width: 1024px;
  height: 425px;
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  background-color: rgba(44, 44, 44, 0.577);
  backdrop-filter: blur(8px);
  border-radius: 20px;
  transition: .3s ease;

}

.welcome-text:hover{
  /* font-size: 2rem; */
  box-shadow: 0 0 15px rgba(17, 252, 17, 0.89);
  
  
}

.welcome-text>h1 {
  color: #fff;
  font-size: 2.2rem;
  /* font-size: 2.6vw; */
  font-family: monospace;
  margin-top: 15px;
}

.welcome-text h2 .code-font {
  color: rgb(16, 225, 16);
}

green {
  color: rgb(16, 225, 16);
}

.welcome-text>h2 {
  margin-top: -0.7rem;
  color: #fff;
  font-size: 4rem;
  /* font-size: 4.6vw; */
  font-family: system-ui;
}

.welcome-text>p {
  margin-top: 1.2rem;
  /* margin-top: 1.5vw; */
  color: #fff;
  font-size: 1.1rem;
  line-height: 1.75;
  /* font-size: 1.32vw; */
  font-family: system-ui;
  padding: 0px 35px;
  text-align: center;
}

.hero-btns {
  margin-top: 32px;
}

.hero-btns .btn {
  margin: 6px;
}

.fa-play-circle {
  margin-left: 4px;
}

.social-icons {
  margin-top: 1rem;
  font-size: 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  letter-spacing: 5px;
  
}

.social-icons i{
  color: white;
  font-size: 1.7rem;
  transition: .3s ease;
}

.social-icons i:hover{
  /* font-size: 2rem; */
  transform: scale(1.15);
  color: rgb(16, 225, 16);
  
}

.social-icons a{
  text-decoration: none;
  color: #fff;
}

.social-icons i{
  padding:0px 10px;
  padding:0vw 1vw;
}


.resume-button{
  margin-top: 2rem;
}

/* @media screen and (max-width: 960px) {
  .welcome-text>h1 {
    font-size: 40px;
    margin-top: -150px;
  }
} */

@media screen and (max-width: 768px) {
  .welcome-text{
    width: 85%;
    height: 500px;
  }
  .welcome-text>h1 {
    font-size: 23px;
    margin-top: 30px;
  }

  .welcome-text>h2 {
    margin-top: 5px;
    font-size: 25px;
  }

  .welcome-text>p{
    margin-top: 30px;
    font-size: 15px;
    /* font-size: 3.5vw; */
  }

  .social-icons {
    margin-top: 5px;
    font-size: 27px;
    /* font-size: 6.5vw; */
  }

  .social-icons a{
    padding: 0px 8px;
  }

  .resume-button{
    margin-top: 30px;
    /* margin-top: 3vw; */
  }
  

  .btn-mobile {
    display: block;
    text-decoration: none;
  }

  .btn {
    width: 100%;
  }
}
