.projects_container {
    background-color: #090909;
    padding: 4.5rem 0rem;
    color: white;
    width: 100%;
    scroll-snap-align: start;
}


.projects_container .project_container_title {
    text-align: center;
    color: #fff;
    font-family: monospace;
    font-size: 35px;
    padding-bottom: 35px;
}

.projects_container .container{
    padding: 1rem 22%;
}


.projects_container a,
.projects_container a:hover {
    text-decoration: none;
    transition: color 0.3s ease-in-out;
}

.projects_container #pageHeaderTitle {
    margin: 2rem 0;
    text-transform: uppercase;
    text-align: center;
    font-size: 2.5rem;
}

/* Cards */
.projects_container .postcard {
    flex-wrap: wrap;
    display: flex;
    box-shadow: 0 4px 21px -12px rgba(0, 0, 0, 0.66);
    border-radius: 10px;
    margin: 0 0 2rem 0;
    overflow: hidden;
    position: relative;
    color: #ffffff;
    /* width: 100px; */
    margin-left: auto;
    margin-right: auto;
    transition: all .2s ease-in-out;
    box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.304), 0 6px 20px 0 rgba(0, 0, 0, 0.479);
}

.projects_container .postcard:hover{
    box-shadow: 0 0 15px rgba(17, 252, 17, 0.89);
}

.projects_container .postcard.dark {
    background-color: #252525;
}

.projects_container .postcard.light {
    background-color: #e1e5ea;
}

.projects_container .postcard .t-dark {
    color: #252525;
}

.projects_container .postcard a {
    color: inherit;
}

.postcard h1,
.projects_container .postcard .h1 {
    margin-bottom: 0.5rem;
    font-weight: 500;
    line-height: 1.2;
}

.projects_container .postcard .small {
    font-size: 80%;
}

.projects_container .postcard .postcard__title {
    font-size: 1.75rem;
}

.projects_container .postcard .postcard__img {
    max-height: 180px;
    width: 100%;
    object-fit: cover;
    position: relative;
}

.projects_container .postcard .postcard__img_link {
    display: contents;
}

.projects_container .postcard .postcard__bar {
    width: 50px;
    height: 10px;
    margin: 10px 0;
    border-radius: 5px;
    background-color: #424242;
    transition: width 0.2s ease;
}

.projects_container .postcard .postcard__text {
    padding: 1.5rem;
    position: relative;
    display: flex;
    flex-direction: column;
}

.projects_container .postcard .postcard__preview-txt {
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: justify;
    height: 100%;
}

.projects_container .postcard .postcard__tagbox {
    display: flex;
    flex-flow: row wrap;
    font-size: 14px;
    margin: 20px 0 0 0;
    padding: 0;
    justify-content: center;
}

.projects_container .postcard .postcard__tagbox .tag__item {
    display: inline-block;
    background: rgba(83, 83, 83, 0.4);
    border-radius: 10px;
    padding: 5px 10px;
    margin: 0 5px 5px 0;
    cursor: default;
    user-select: none;
    transition: all 0.4s ease-in-out;
    
}

.projects_container .postcard .postcard__tagbox .tag__item:hover {
    background: rgba(83, 83, 83, 0.8);
    transform: scale(1.02);
}

.projects_container .postcard:before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    /* background-image: linear-gradient(-70deg, #424242, transparent 50%); */
    opacity: 1;
    border-radius: 10px;
}

.projects_container .postcard:hover .postcard__bar {
    width: 100px;
}

@media only screen and (max-width: 1240px) {
    .projects_container{
        padding: 4.5rem 0rem;
    }
    
}

@media only screen and (max-width: 1400px) {
    .projects_container .container{
        padding: 1rem 20%;
    }
    
}

@media only screen and (max-width: 1150px) {
    .projects_container .container{
        padding: 1rem 5%;
    }
    
}




@media screen and (min-width: 769px) {

    .projects_container .postcard {
        flex-wrap: inherit;
    }

    .projects_container .postcard .postcard__title {
        font-size: 2rem;
    }

    .projects_container .postcard .postcard__tagbox {
        justify-content: start;
    }

    .projects_container .postcard .postcard__img {
        max-width: 300px;
        max-height: 100%;
        transition: transform 0.3s ease;
    }

    .projects_container .postcard .postcard__text {
        padding: 3rem;
        width: 100%;
    }

    .projects_container .postcard .media.postcard__text:before {
        content: "";
        position: absolute;
        display: block;
        background: #252525;
        top: -20%;
        height: 130%;
        width: 55px;
    }

    .projects_container .postcard:hover .postcard__img {
        transform: scale(1.1);
    }

    .projects_container .postcard:nth-child(2n+1) {
        flex-direction: row;
    }

    .projects_container .postcard:nth-child(2n+0) {
        flex-direction: row-reverse;
    }

    .projects_container .postcard:nth-child(2n+1) .postcard__text::before {
        left: -12px !important;
        transform: rotate(4deg);
    }

    .projects_container .postcard:nth-child(2n+0) .postcard__text::before {
        right: -12px !important;
        transform: rotate(-4deg);
    }
}

@media screen and (min-width: 1024px) {
    .projects_container .postcard__text {
        padding: 2rem 3.5rem;
    }

    .projects_container .postcard__text:before {
        content: "";
        position: absolute;
        display: block;
        top: -20%;
        height: 130%;
        width: 55px;
    }

    .projects_container .postcard.dark .postcard__text:before {
        background: #252525;
    }

    .projects_container .postcard.light .postcard__text:before {
        background: #e1e5ea;
    }
}


.projects_container .postcard:hover .postcard__title{
    color: rgb(16, 225, 16);
}

.projects_container .blue .postcard__bar {
    background-color: rgb(16, 225, 16);;
}

