:root {
    --primary: #fff;
    --green: rgb(19, 209, 19);
}

.btn {
    padding: 8px 20px;
    border-radius: 10px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary {
    background-color: var(--primary);
    color: #242424;
    border: 1px solid var(--primary);
}

.btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 2px solid var(--green);
    transition: all 0.3s ease-out;
    
}

.btn--medium {
    padding: 8px 20px;
    font-size: 18px;
    font-family: system-ui;
    font-weight: 500;
}

.btn--large {
    padding: 12px 26px;
    font-size: 20px;
}

.btn--medium:hover,
.btn--large:hover {
    background: var(--green);
    color: #242424;
    transition: all 0.3s ease-out;
}