@charset "UTF-8";

.resume-container {
    background-color: #090909;
    padding: 4.5rem 5rem;
    color: white;
    width: 100%;
    padding-bottom: 100px;

}

@media only screen and (min-width: 300px) {
  
    .resume-container {
      padding: 4.5rem 0rem;
    }
    
  }
  

.resume-section-title h1 {
    text-align: center;
    color: #fff;
    font-family: monospace;
    font-size: 35px;
    padding-bottom: 4rem;

}

.resume-content{
    width: 90%;
    margin: 0 auto;
}

.hr_green {
    display: block;
    height: 1px;
    border: 0;
    border-top: 2px solid #048300;
    margin: 0.5em 0;
    padding: 0;
}

.hr_white {
    display: block;
    height: 1px;
    border: 0;
    border-top: 2px solid white;
    margin: 0.5em 0;
    padding: 0;
}

.hr_red {
    display: block;
    height: 1px;
    border: 0;
    border-top: 2px solid #b01030;
    margin: 0.5em 0;
    padding: 0;
}


.timeline-container {
    background: black;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    width: 100vw;
    margin: 0;
    padding: 2vh 100px;
    font-family: "Source Sans Pro", arial, sans-serif;
    font-weight: 300;
    color: rgb(60, 224, 28);
    box-sizing: border-box;
}



.timeline {
    width: 100%;
    max-width: 800px;
    background: rgb(0, 0, 0);
    padding: 100px 50px;
    position: relative;
    box-shadow: 0.5rem 0.5rem 2rem 0 rgba(0, 0, 0, 0.2);
}

.timeline:before {
    content: "";
    position: absolute;
    top: 0px;
    left: calc(33% + 15px);
    bottom: 0px;
    width: 4px;
    background: #ddd;
}

.timeline:after {
    content: "";
    display: table;
    clear: both;
}

.entry {
    clear: both;
    text-align: left;
    position: relative;
}

.entry .title {
    margin-bottom: 0.5em;
    float: left;
    width: 33%;
    padding-right: 30px;
    text-align: right;
    position: relative;
}

.entry .title:before {
    content: "";
    position: absolute;
    width: 8px;
    height: 8px;
    border: 4px solid salmon;
    background-color: #fff;
    border-radius: 100%;
    top: 15%;
    right: -8px;
    z-index: 99;
}

.entry .title h3 {
    margin: 0;
    font-size: 120%;
}

.entry .title p {
    margin: 0;
    font-size: 100%;
}

.entry .body {
    margin: 0 0 3em;
    float: right;
    width: 66%;
    padding-left: 30px;
}

.entry .body p {
    line-height: 1.4em;
}

.entry .body p:first-child {
    margin-top: 0;
    font-weight: 400;
}

.entry .body ul {
    color: rgb(255, 255, 255);
    padding-left: 0;
    list-style-type: none;
}

.entry .body ul li:before {
    content: "–";
    margin-right: 0.5em;
}

h4{
    font-size: 17px;

}

.logo_icon{
    width: 60px;
    height: 60px;
    border-radius: 20px;
}

@media only screen and (max-width: 1170px) {
  
    .resume-container {
      padding: 4.5rem 0rem;
    }
    .logo_icon{
        width: 40px;
        height: 40px;
        border-radius: 20px;
    }
  }
  