.about-me {
    background-color:#141414;
    padding: 4.5rem 5rem;
    color: white;
    width: 100%;
    padding-bottom: 7rem;
    scroll-snap-align: start;
}

.about-me h1 {
    text-align: center;
    color: #fff;
    font-family: monospace;
    font-size: 35px;
    padding-bottom: 4rem;

}

.center_align {
    justify-content: center;
    display: flex;
}

.about-me-contents {
    display: inline-block;
    align-items: center;
    width: 1295px;
    margin: 0 auto;
    vertical-align: top;
}

.about-me .left-section {
    width: 30%;
    padding: 1rem 4rem;
    float: left;
    
}


.about-me .left-section img {
    width: 250px;
    border-radius: 20px;
    /* border: 2px solid green; */
    box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.304), 0 6px 20px 0 rgba(0, 0, 0, 0.479);
    transition: all .2s ease-in-out;
    background-color: rgb(37, 41, 36);

}

.about-me .left-section img:hover{
    box-shadow: 0 0 15px rgba(17, 252, 17, 0.89);
}

.about-me .right-section {
    width: 70%;
    padding: 2rem 2rem;
    float: right;
    font-family: "Calibre", "Inter", "San Francisco", "SF Pro Text", -apple-system, system-ui, sans-serif;
    font-size: 18px;
    font-weight: 400;
    background-color: #252525;
    border-radius: 15px;
    /* border: 3px solid #048300; */
    transition: all .2s ease-in-out;
    box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.304), 0 6px 20px 0 rgba(0, 0, 0, 0.479);
    
}

.about-me .right-section:hover{
    box-shadow: 0 0 15px rgba(17, 252, 17, 0.89);
}

ul.skills-list {
    display: grid;
    grid-template-columns: repeat(2, minmax(140px, 200px));
    grid-gap: 0 10px;
    padding: 0;
    margin: 20px 0 0 0;
    overflow: hidden;
    list-style: none;
}

ul.skills-list li {
    position: relative;
    margin-bottom: 10px;
    padding-left: 20px;
    font-family: monospace;
    font-size: 17px;
}

ul.skills-list li::before {
    content: '▶';
    position: absolute;
    left: 0;
    color: rgb(14, 229, 14);
    ;
    font-size: 10px;
    line-height: 20px;
}


@media only screen and (max-width: 1240px) {
    


    .about-me .left-section {
      display: block;
      padding: 1.5rem 0rem;
      width: 100%;
      text-align: center;
      
    }
    .about-me .right-section {
        display: block;
        width: 100%;
    }
    
  }
  

